import React from 'react'
import Head from 'next/head'
import { AppBar, AppContent, Drawer } from '../components'
import { TopAppBarFixedAdjust } from '@material/react-top-app-bar'
import '../index.scss'

class AppLayout extends React.Component {
  state = {
    isDrawerOpen: false,
  }
  setIsDrawerOpen = isDrawerOpen => {
    this.setState({ isDrawerOpen })
  }
  render () {
    return (
      <div>
        <Head>
          <title>Material Next</title>
          <link href="https://fonts.googleapis.com/icon?family=Material+Icons" rel="stylesheet" />
          <link href="https://fonts.googleapis.com/css?family=Roboto" rel="stylesheet" />
          <meta name="viewport" content="initial-scale=1.0, width=device-width" />
        </Head>
        <div className='desktop-drawer-container'>
          <Drawer
            isOpen={this.state.isDrawerOpen}
            setIsDrawerOpen={this.setIsDrawerOpen}
          />
          <div className='desktop-drawer-app-content'>
            <AppBar
              setIsDrawerOpen={this.setIsDrawerOpen}
            />
            <TopAppBarFixedAdjust>
              <AppContent />
            </TopAppBarFixedAdjust>
          </div>
        </div>
        <style jsx>{`
          @media (min-width: 840px) {
            .desktop-drawer-container {
              display: flex;
              flex-direction: row;
              height: 100vh;
              overflow: hidden;
            }
            .desktop-drawer-app-content {
              flex: auto;
              overflow: auto;
            }
          }
        `}</style>
      </div>
    )
  }
}

export default AppLayout
